html, body {
  padding: 0;
  margin: 0;
  font-family: "Gotham-Book"; }
body {
  background-color: #282828; }
@media (max-width: 767px) {
    body {
      background-color: #1b1b1b; } }
#app > .wrapper {
  max-width: 980px;
  width: 100%;
  margin-left: auto;
  margin-right: auto; }
.project-item {
  position: relative;
  margin-bottom: 2em; }
@media (max-width: 767px) {
    .project-item {
      margin-bottom: 4em; } }
img {
  width: 100%; }
.title {
  position: absolute;
  background-color: #282828;
  left: 0.8em;
  top: 0.8em;
  padding: 0.0em 1.0em;
  padding-top: 0.2em;
  vertical-align: center;
  line-height: 3em;
  height: 3em; }
.title > span {
    text-transform: uppercase;
    font-family: "Gotham-Black";
    font-size: 2em;
    color: #fbd651; }
@media (max-width: 767px) {
    .title {
      position: static;
      width: 100%;
      padding: 0;
      padding-top: 2em;
      padding-bottom: 2em; }
      .title > span {
        font-size: 3em;
        margin-left: 1em; } }
.info-container {
  position: absolute;
  left: 0.8em;
  top: 4.5em;
  vertical-align: center; }
@media (max-width: 767px) {
    .info-container {
      position: static;
      width: 100%; } }
.info {
  background-color: #282828;
  color: #fbd651;
  font-size: 1em;
  padding: 0.4em 0.4em;
  padding-bottom: 0.2em;
  text-transform: uppercase;
  display: inline-block;
  line-height: 1.2em;
  vertical-align: center; }
@media (max-width: 767px) {
    .info {
      font-size: 3em;
      display: block; }
      .info > span {
        font-size: 0.6em;
        margin-left: 1em; } }
@media (max-width: 767px) {
  .date {
    display: none; } }
.tag-container {
  font-size: 1em; }
@media (min-width: 768px) {
    .tag-container {
      position: absolute;
      bottom: 0.8em;
      left: 0.8em;
      right: 1em; } }
@media (max-width: 767px) {
    .tag-container {
      display: none; } }
.tag {
  background-color: #FFFFFF;
  color: #282828;
  text-decoration: none;
  margin-right: 0.3em;
  padding: 0.3em 0.3em;
  padding-bottom: 0.2em;
  font-size: 1em;
  cursor: pointer;
  -webkit-transition: background-color 0.2s, color 0.2s;
  transition: background-color 0.2s, color 0.2s;
  display: inline-block;
  margin-bottom: 0.1em;
  margin-top: 0.1em; }
@media (max-width: 767px) {
    .tag {
      font-size: 2em;
      border: solid 1px #CCCCCC; } }
.tag:hover {
  background-color: #282828;
  color: #fbd651; }
.link {
  cursor: pointer;
  color: #fbd651;
  -webkit-transition: color 0.2s;
  transition: color 0.2s; }
.link:hover {
  color: #FFFFFF; }





































